.upperCard,.lowerCard{display:flex;position:relative;justify-content:center;width:100%;height:50%;overflow:hidden;border:1px solid #f5f5f5}.upperCard span,.lowerCard span{font-size:4em;font-family:"Droid Sans Mono",monospace;font-weight:lighter;color:#14b014}.upperCard{align-items:flex-end;border-bottom:0.5px solid #f5f5f5;border-top-left-radius:2px;border-top-right-radius:2px}.upperCard span{-webkit-transform:translateY(50%);transform:translateY(50%)}.lowerCard{align-items:flex-start;border-top:0.5px solid #f5f5f5;border-bottom-left-radius:2px;border-bottom-right-radius:2px}.lowerCard span{-webkit-transform:translateY(-50%);transform:translateY(-50%)}.ie .upperCard span,.ie .lowerCard span{color:#0055B2;font-size:3.7em}@media only screen and (max-device-width: 667px), screen and (max-width: 599px){.upperCard span,.lowerCard span{font-size:3.2em}}
