html, body, #app {
  height: 100%;
}
body {
  font-family: sans-serif;
  margin: 0;
  padding: 0;
}

*{box-sizing:border-box}html,body{min-height:100%;overflow:hidden}body{margin:0;background-color:#FFF;background-image:linear-gradient(62deg, #0d700d 0%, #009b00 35%, #00d300 100%);background-size:auto 100%;width:100%;min-height:100vh;display:flex;display-align-items:center;display-justify-content:center}#app{display:flex;position:relative;width:100%;min-height:100vh;justify-content:center;align-items:center}.header{text-align:center;position:relative;font-family:'Droid Sans';font-weight:lighter;text-transform:uppercase;letter-spacing:0.17em;color:white}

.upperCard,.lowerCard{display:flex;position:relative;justify-content:center;width:100%;height:50%;overflow:hidden;border:1px solid #f5f5f5}.upperCard span,.lowerCard span{font-size:4em;font-family:"Droid Sans Mono",monospace;font-weight:lighter;color:#14b014}.upperCard{align-items:flex-end;border-bottom:0.5px solid #f5f5f5;border-top-left-radius:2px;border-top-right-radius:2px}.upperCard span{-webkit-transform:translateY(50%);transform:translateY(50%)}.lowerCard{align-items:flex-start;border-top:0.5px solid #f5f5f5;border-bottom-left-radius:2px;border-bottom-right-radius:2px}.lowerCard span{-webkit-transform:translateY(-50%);transform:translateY(-50%)}.ie .upperCard span,.ie .lowerCard span{color:#0055B2;font-size:3.7em}@media only screen and (max-device-width: 667px), screen and (max-width: 599px){.upperCard span,.lowerCard span{font-size:3.2em}}

@-webkit-keyframes fold{0%{-webkit-transform:rotateX(0deg);transform:rotateX(0deg)}100%{-webkit-transform:rotateX(-180deg);transform:rotateX(-180deg)}}@keyframes fold{0%{-webkit-transform:rotateX(0deg);transform:rotateX(0deg)}100%{-webkit-transform:rotateX(-180deg);transform:rotateX(-180deg)}}@-webkit-keyframes unfold{0%{-webkit-transform:rotateX(180deg);transform:rotateX(180deg)}100%{-webkit-transform:rotateX(0deg);transform:rotateX(0deg)}}@keyframes unfold{0%{-webkit-transform:rotateX(180deg);transform:rotateX(180deg)}100%{-webkit-transform:rotateX(0deg);transform:rotateX(0deg)}}.flipCard{display:flex;justify-content:center;position:absolute;left:0;width:155px;height:60px;overflow:hidden;-webkit-backface-visibility:hidden;backface-visibility:hidden}.flipCard span{font-family:"Droid Sans Mono",monospace;font-size:4em;font-weight:lighter;color:#14b014}.flipCard.unfold{top:50%;align-items:flex-start;-webkit-transform-origin:50% 0%;transform-origin:50% 0%;-webkit-transform:rotateX(180deg);transform:rotateX(180deg);background-color:#fff;border-bottom-left-radius:3px;border-bottom-right-radius:3px;border:1px solid #f5f5f5;border-top:0.5px solid #f5f5f5}.flipCard.unfold span{-webkit-transform:translateY(-50%);transform:translateY(-50%)}.flipCard.fold{top:0%;align-items:flex-end;-webkit-transform-origin:50% 100%;transform-origin:50% 100%;-webkit-transform:rotateX(0deg);transform:rotateX(0deg);background-color:#fff;border-top-left-radius:3px;border-top-right-radius:3px;border:0.5px solid #f5f5f5;border-bottom:0.5px solid #f5f5f5}.flipCard.fold span{-webkit-transform:translateY(50%);transform:translateY(50%)}.fold{-webkit-animation:fold .6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;animation:fold .6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;-webkit-transform-style:preserve-3d;transform-style:preserve-3d}.unfold{-webkit-animation:unfold .6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;animation:unfold .6s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;-webkit-transform-style:preserve-3d;transform-style:preserve-3d}.foldSlower{-webkit-animation:fold 1s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;animation:fold 1s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;-webkit-transform-style:preserve-3d;transform-style:preserve-3d}.unfoldSlower{-webkit-animation:unfold 1s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;animation:unfold 1s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;-webkit-transform-style:preserve-3d;transform-style:preserve-3d}.foldSlowest{-webkit-animation:fold 1.5s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;animation:fold 1.5s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;-webkit-transform-style:preserve-3d;transform-style:preserve-3d}.unfoldSlowest{-webkit-animation:unfold 1.5s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;animation:unfold 1.5s cubic-bezier(0.455, 0.03, 0.515, 0.955) 0s 1 normal forwards;-webkit-transform-style:preserve-3d;transform-style:preserve-3d}.ie .flipCard span{color:#0055B2;font-size:3.7em}@media only screen and (max-device-width: 667px), screen and (max-width: 599px){.flipCard{width:90px;height:40px}.flipCard span{font-size:3.2em}}

.flipUnitContainer{display:block;position:relative;width:155px;height:120px;-webkit-perspective-origin:50% 50%;perspective-origin:50% 50%;-webkit-perspective:300px;perspective:300px;background-color:#fff;border-radius:3px;box-shadow:0px 10px 10px -10px #0a540a;margin:0 5px}.flipUnitContinerLabel{text-align:center;display:block;font-family:'Droid Sans';margin:5px 0;color:#0c6b0c}.ie .flipUnitContainer{box-shadow:0px 10px 10px -10px #0055B2}.ie .flipUnitContinerLabel{color:whitesmoke}@media only screen and (max-device-width: 667px), screen and (max-width: 599px){.flipUnitContainer{width:90px;height:80px;margin:0 2px}}

